import { initIndex } from "./views/index";
import { initEmpezarPage } from "./views/empezar";
import { initJuego } from "./views/juego";

export function initRouter(root: Element) {
  function goTo(path) {
    history.pushState({}, "", path);
    handleRoute(path);
  }

  function handleRoute(route) {
    const routes = [
      {
        path: /\/dwf-m5-desafio\//,
        component: initIndex,
      },
      {
        path: /\/dwf-m5-desafio\/empezar/,
        component: initEmpezarPage,
      },
      {
        path: /\/dwf-m5-desafio\/juego/,
        component: initJuego,
      },
    ];

    // const routes = [
    //     {
    //       path: "/",
    //       component: initIndex,
    //     },
    //     {
    //       path: "/empezar",
    //       component: initEmpezarPage,
    //     },
    //     {
    //       path: "/juego",
    //       component: initJuego,
    //     },
    //   ];

    for (const r of routes) {
      // if (r.path == route) {
      if (r.path.test(route)) {
        const el = r.component({ goTo: goTo });
        if (root.firstChild) {
          root.firstChild.remove();
        }
        root.appendChild(el);
      }
    }
  }

  handleRoute(location.pathname);
}
